body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body,html {
    overflow-x: hidden;
    font-family:Geneva, Verdana, sans-serif;
    font-weight: bold;
    letter-spacing: 1.5vh;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.7;
}

.webgl{
    position:absolute;
    top: 0;
    left: 0;
    z-index: 1;
}

.container{
    position: absolute;
    z-index: 2;
    color: white;
    left: 5%;
    top:35%;
}

.title{
    color: rgb(82, 82, 82);
}

.blue{
    color: rgb(0, 157, 255);
}

.green{
    color: rgb(8, 226, 0);
}

.time{
    color: white;
    z-index: 2;
    position:absolute;
    top: 5vw;
    left: 75vw;
    display: flex;
    flex-direction: column;
    width: 24vw;
    padding-right: 5vw;
}

.text{

    letter-spacing: 0.3vh;
    word-spacing: 0.3vh;
}

.count{
    display: flex;
    flex-wrap: wrap;
    width: 20vw;
}

.content:first-of-type {
animation: showup 60s infinite;
}


@keyframes showup {
0% {opacity:0;}
20% {opacity:1;}
80% {opacity:1;}
100% {opacity:0;}
}
